.p_EidtBlock {
    /* padding: 10px; */
    width: 100%;
    color: #d9e0e9 ;
}

.p_EidtBlock .p_Scroll {
    height: calc( 100% - 120px );
}


.p_EidtBlock .zeact_row {
    align-items: flex-start;
    padding: 10px;
}

.p_EidtBlock .p_EidDiv,.p_EidtBlock .p_MarkDown {
    position: relative;
    padding: 0px 4px 0px 0px;
    min-height: 300px;
}

.p_EidtBlock .p_MarkDown {
    padding: 0px 0px 0px 10px;
}

.p_EidtBlock .p_EidDiv:after {
    content: '';
    position: absolute;
    right: 0px;
    top:0px;
    height: 100%;
    width: 0.5px;
    background-color: #1b1d21;
}


.p_EidtBlock .button {
    margin-top: 18px;
}

.p_EidtBlock .button .zeact_button {
    margin: 0px 8px;
}