.p_Popup_block{
    position: fixed;
    z-index: 99;
    top: 0px;
    left: 0px;
    height: 100%;
    width: 100%;
}

.p_Popup_block .p_Popup_background {
    position: absolute;
    background-color: rgba(0,0,0,0.4);
    top: 0px;
    left: 0px;
    height: 100%;
    width: 100%;
    animation: popup .4s;
    -webkit-animation: popup .4s;
    
}

.p_Popup_block .p_Popup_info{
    position: absolute;
    top: 15%;
    width: 100%;
    max-width: 1096px;
    height: 85%;
    background-color: #333639;
    box-shadow: 0px 0px 25px #090a0a;
    border-radius: 6px 6px 0px 0px;
    opacity: 1;
    animation: popupinfo .4s;
    -webkit-animation: popupinfo .4s;
}

.p_Popup_block.hied .p_Popup_background {
    opacity: 0;
    transition: opacity .4s;
    -webkit-transition: opacity .4s; /* Safari */
}

.p_Popup_block.hied .p_Popup_info {
    top: 100%;
    transition: top .4s;
    -webkit-transition: top .4s; /* Safari */
}

@media screen and (min-width: 1096px) {
    .p_Popup_block .p_Popup_info {
        left: 50%;
        margin-left: -548px;
    }
}

@keyframes popup
{
    0%   {background-color: rgba(0,0,0,0);}
    100% {background-color: rgba(0,0,0,0.4);}
}
 
@-webkit-keyframes popup /* Safari 与 Chrome */
{
    0%   {background-color: rgba(0,0,0,0);}
    100% {background-color: rgba(0,0,0,0.4);}
}

@keyframes popupinfo
{
    0%   {
        /* opacity: 0; */
        top: 100%;
    }
    100% {
        opacity: 1;
        top: 15%;
    }
}
 
@-webkit-keyframes popupinfo /* Safari 与 Chrome */
{
    0%   {
        /* opacity: 0; */
        top: 100%;
    }
    100% {
        opacity: 1;
        top: 15%;
    }
}