.p_Sreach {
    background-color: #232527;
    margin: 8px 0px;
    overflow: hidden;
    border-radius: 4px;
    padding: 2px 0px;
}

.p_Sreach .zeact_row {
    padding: 0px 2px;
}

.p_Sreach .p_EidDiv .input, .p_Sreach .p_EidDiv .placeholder{
    /* margin: 0px 6px; */
    width: calc( 100% - 12px );
    margin-left: 6px;
}

.page_list {
    margin-bottom: 10px;
}

.title {
    padding: 8px 0px 2px;
}

.p_Scroll.index_scroll {
    position: absolute;
    height: calc( 100% - 104px );
    width: 100%;
}

@media screen and (max-width: 750px) {
 
    /* .z_gcg_nav_title a {
        display: none;
    } */
    .p_Scroll.index_scroll {
        width: calc( 100% - 16px );
    }
    
}


.p_Popup_info .p_EidtBlock {
    /* padding: 10px; */
    width: 100%;
    height: 100%;
    position: relative;
}

