.zy_bot_bor,.zy_top_bor {
    position: relative;
}

.zy_bot_bor:after, .zy_top_bor:before {
    content: '';
    position: absolute;
    left: 0;
    bottom: 0;
    right: auto;
    top: auto;
    height: 1px;
    width: 100%;
    background-color: rgba(0, 0, 0, 0.12);
    display: block;
    z-index: 15;
    -webkit-transform-origin: 50% 100%;
    transform-origin: 50% 100%;
    -webkit-transform: scaleY(0.5);
    transform: scaleY(0.5);
}

.zy_top_bor:before {
    top: 0;
}
/*弹性滚动卡主的bug*/
.touch_bug{
    position: relative;
    min-height: calc(100% - 38px)
}
