.p_LoadBlock {
    position: relative;
    height: 100%;
}

.p_LoadBlock_back {
    position: absolute;
    top : 0px;
    width: 100%;
    height: 100%;
    background-color: #343639;
    z-index: 1;
    opacity: 1;
    /* display: flex;
    align-items: center; */
}

.p_LoadBlock_back.hidden {
    opacity: 0;
    transition: opacity .3s;
    -moz-transition: opacity .3s;	/* Firefox 4 */
    -webkit-transition: opacity .3s;	/* Safari 和 Chrome */
    -o-transition: opacity .3s;	/* Opera */
}

.circle {

    margin: 40px auto;
    position: relative;
    width: 8px;
    height: 8px;
    background-color: 
    rgba(255,255,255,.5);
    box-shadow: -14px 0px 0px
    rgba(255,255,255,1);
    border-radius: 50%;
    -webkit-animation: circle_classic 1s ease-in-out infinite alternate;
    -moz-animation: circle_classic 1s ease-in-out infinite alternate;
    animation: circle_classic 1s ease-in-out infinite alternate;

}

.typing_loader{
    width: 6px;
    height: 6px;
    border-radius: 50%;
    -webkit-animation: typing 1s linear infinite alternate;
       -moz-animation: Typing 1s linear infinite alternate;
            animation: typing 1s linear infinite alternate;
    margin: 46px auto; /* Not necessary- its only for layouting*/  
    position: relative;
    left: -12px;
}
@-webkit-keyframes typing{
    0%{
        background-color: rgba(255,255,255, 1);
        box-shadow: 12px 0px 0px 0px rgba(255,255,255,0.2), 
                    24px 0px 0px 0px rgba(255,255,255,0.2);
      }
    25%{ 
        background-color: rgba(255,255,255, 0.4);
        box-shadow: 12px 0px 0px 0px rgba(255,255,255,2), 
                    24px 0px 0px 0px rgba(255,255,255,0.2);
    }
    75%{ background-color: rgba(255,255,255, 0.4);
        box-shadow: 12px 0px 0px 0px rgba(255,255,255,0.2), 
                    24px 0px 0px 0px rgba(255,255,255,1);
      }
}

@-moz-keyframes typing{
   0%{
        background-color: rgba(255,255,255, 1);
        box-shadow: 12px 0px 0px 0px rgba(255,255,255,0.2), 
                    24px 0px 0px 0px rgba(255,255,255,0.2);
      }
    25%{ 
        background-color: rgba(255,255,255, 0.4);
        box-shadow: 12px 0px 0px 0px rgba(255,255,255,2), 
                    24px 0px 0px 0px rgba(255,255,255,0.2);
    }
    75%{ background-color: rgba(255,255,255, 0.4);
        box-shadow: 12px 0px 0px 0px rgba(255,255,255,0.2), 
                    24px 0px 0px 0px rgba(255,255,255,1);
      }
}

@keyframes typing{
   0%{
        background-color: rgba(255,255,255, 1);
        box-shadow: 12px 0px 0px 0px rgba(255,255,255,0.2), 
                    24px 0px 0px 0px rgba(255,255,255,0.2);
      }
    25%{ 
        background-color: rgba(255,255,255, 0.4);
        box-shadow: 12px 0px 0px 0px rgba(255,255,255,2), 
                    24px 0px 0px 0px rgba(255,255,255,0.2);
    }
    75%{ background-color: rgba(255,255,255, 0.4);
        box-shadow: 12px 0px 0px 0px rgba(255,255,255,0.2), 
                    24px 0px 0px 0px rgba(255,255,255,1);
      }
}


@keyframes circle_classic
{
    0% {

        opacity: 0.1;
        -webkit-transform: rotate(0deg) scale(0.5);
    
    }
    100% {
    
        opacity: 1;
        -webkit-transform: rotate(360deg) scale(1.2);
    
    }
}
 
@-webkit-keyframes circle_classic /* Safari 与 Chrome */
{
    0% {

        opacity: 0.1;
        -webkit-transform: rotate(0deg) scale(0.5);
    
    }
    100% {
    
        opacity: 1;
        -webkit-transform: rotate(360deg) scale(1.2);
    
    }
}
