.zeact_button {
    padding: 4px 14px;
    display: flex;
    border-radius: 4px;
    justify-content: center;
    align-content: center;
    position: relative;
    overflow: hidden;
}

.zeact_button .zy_loader_icon {
    height: 14px;
}

.zeact_button_load {
    position: absolute;
    opacity: 1;
    width: 100%;
    height: 100%;
    top: 0px;
    left: 0px;
    display: flex;
    justify-content: center;
    align-content: center;
    animation: load .3s;
    -webkit-animation: load .3s;
    border-radius: 4px;
    pointer-events: none;
}

.zeact_button_load.hidden {
    opacity: 0;
    transition: opacity .3s;
    -moz-transition: opacity .3s;	/* Firefox 4 */
    -webkit-transition: opacity .3s;	/* Safari 和 Chrome */
    -o-transition: opacity .3s;	/* Opera */
}

.zeact_button_load .typing_loader{
    margin: 4px auto;
    width: 3px;
    height: 3px;
}

@keyframes load
{
    0%   {opacity: 0;}
    100% {opacity: 1;}
}
 
@-webkit-keyframes load /* Safari 与 Chrome */
{
    0%   {opacity: 0;}
    100% {opacity: 1;z-index: 0;}
}