.p_EidDiv {
    position: relative;
}

.p_EidDiv p {
    margin: 1em 0px !important;
}


.p_EidDiv .input:focus { 
    outline: none; 
} 

.input:empty+.placeholder {
    opacity:1;
}

.p_EidDiv .placeholder {
    position: absolute;
    top: 0px;
    color: #55575d;
    opacity:0;
    /* z-index: -1; */
    transition: opacity .2s;
    -moz-transition: opacity .2s;	/* Firefox 4 */
    -webkit-transition: opacity .2s;	/* Safari 和 Chrome */
    -o-transition: opacity .2s;	/* Opera */
}

.p_EidDiv .input{
    height: 100%;
    min-height:-webkit-fill-available;
    position: relative;
    width: 100%;
    z-index: 1;
}

